import React, { useState } from 'react';
import LocalizedStrings from 'react-localization';
import { createContext } from "react"

export const LanguageContext = createContext();

let strings = new LocalizedStrings({
    en: {
        //pages
        messenger: "Messenger",
        settings: "Settings",
        friends: "Friends",
        recommendations: "Recommendations",
        communities: "Communities",
        news: "News",
        //login
        singIn: "Sing-in",
        singUp: "Sing-up",
        forgotPass: "Forgot your password?",
        pass: "password",
        repeatPass: "repeat password",
        login: "login",
        doSingIn: "Sing-in",
        doSingUp: "Sing-up",
        valPassLength: "Password length is less than eight characters",
        valPassLetters: "The password must include upper and lowercase letters of the Latin alphabet",
        valPassSymbols: "The password must include any two characters",
        valRepPass: "Password mismatch",
        valEmail: 'Enter email',
        valLogin: 'Enter login',
        //settings
        setColorTheme: 'Choose a color scheme',
        light: 'Light',
        dark: 'Dark',
        setLanguagee: 'Choose language',
        logOut: 'Log out of your account',
        // infoModal
        firstName: "First Name",
        lastName: "Last Name",
        patronymic: "Patronymic",
        placeOfResidence: "Place of residence",
        familyStatus: "Family status",
        tellAboutYourself: "Tell us about yourself",
        save: "Save",
        //photoModal
        comments: "Comments",
        writeComment: "Write a comment",

    },
    ru: {
        //pages
        messenger: "",
        settings: "",
        friends: "",
        recommendations: "",
        communities: "",
        news: "",
        //login
        singIn: "",
        singUp: "",
        forgotPass: " ",
        pass: "",
        repeatPass: " ",
        login: "",
        doSingIn: "",
        doSingUp: "",
        valPassLength: "    ",
        valPassLetters: "        ",
        valPassSymbols: "     ",
        valRepPass: "  ",
        valEmail: '  ',
        valLogin: ' ',
        //settings
        setColorTheme: '  ',
        light: '',
        dark: '',
        setLanguagee: ' ',
        logOut: '  ',
        // infoModal
        firstName: "",
        lastName: "",
        patronymic: "",
        placeOfResidence: " ",
        familyStatus: " ",
        tellAboutYourself: "  ",
        save: "",
        //photoModal
        comments: "",
        writeComment: " ",


    }
});

export const LanguageProvider = ({ children }) => {
    const [language, setLanguage] = useState(strings.getInterfaceLanguage());
    const changeLanguage = () => {
        const newLanguage = language === 'en' ? 'ru' : 'en';
        strings.setLanguage(newLanguage);
        setLanguage(newLanguage);
    };
    
    return (
        <LanguageContext.Provider value={{ strings, changeLanguage, language, }}>
            {children}
        </LanguageContext.Provider>
  );
};
